<template>
  <div class="index">
    <div class="body-left" :class="{'body-left-close':menuObj.isCollapse}">
      <div class="middle">
        <Menus class="mynavbar" @menuTreeChange="menuTreeChange" :menuObj="menuObj"></Menus>
      </div>
    </div>
    <div class="body-right">
      <div class="header-wrap">
        <Headers :menuTree="menuTree" :headObj="headObj" @toggleOpen="toggleOpen"></Headers>
      </div>
      <div class="container">
        <div class="content-wrap">
          <div class="contents">
            <transition name="fade" mode="out-in">
              <router-view></router-view>
            </transition>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style>
/* 样式改版 */
.el-menu--collapse .el-submenu .el-menu {
  margin-left: 0;
}
.body-left-close .firstTitle {
  color: rgba(0, 0, 0, 0);
}
.body-left .middle {
  overflow: auto;
  /* width: 220px; */
  padding: 0 0 60px 0;
  box-sizing: border-box;
  height: 100vh;
}
.body-left-close .middle {
  background: url(https://www.haohuan.com/img/haohuan.3.0/hfq-logo.png)
    no-repeat 0 0;
  background-size: 64px auto;
  padding: 60px 0;
  overflow: hidden;
}
.body-left-close:hover .middle {
  width: 100vw;
  height: 100vh;
  overflow-y: auto;
}
.body-left-close {
  width: 64px;
}
.middle::-webkit-scrollbar {
  display: none;
}

.body-left {
  float: left;
  height: 100vh;
  background: #2a3245;
}
.body-right {
  width: auto;
  overflow-y: auto;
  height: 100vh;
  overflow-x: auto;
}

.index {
  height: 100%;
  width: 100%;
  min-width: 950px;
}
.header-wrap {
  z-index: 100;
  position: relative;
  height: 70px;
}
.container {
  position: relative;
  height: -moz-calc(100% - 70px);
  height: -webkit-calc(100% - 70px);
  height: calc(100% - 70px);
  background: #fff;
}
.mynavbar {
  height: 100%;
  /*text-align: center;*/
  background-color: #2a3245;
  overflow: auto;
}
.content-wrap {
}
.contents {
  height: 100%;
  width: 100%;
  overflow-y: auto;
  min-height: 720px;
  min-height: 80vh;
}
.search-wrapper {
  display: flex;
  display: -webkit-flex;
  display: -ms-flex;
  margin: 0px auto 10px;
}

.search-content {
}

.search-btn {
  margin-left: 5px;
}

.el-table__header {
  font-size: 13px;
}
.right {
  float: right;
}
</style>
<script>
import "../assets/style/base.css";
import Headers from "../components/head.vue";
import Menus from "../components/menu.vue";
import menuTree from "../config/menu";
export default {
  data() {
    return {
      menuTree: [],
      headObj: {
        isCollapse: true,
      },
      menuObj: {
        isCollapse: true,
      },
    };
  },
  methods: {
    menuTreeChange(arr) {
      this.menuTree = arr;
    },
    toggleOpen() {
      this.headObj.isCollapse = !this.headObj.isCollapse;
      this.menuObj.isCollapse = !this.menuObj.isCollapse;
    },
  },
  components: {
    Headers,
    Menus,
  },
};
</script>
