-dropdown-item>\r\n            <el-dropdown-item @click.native=\"logout\">退出</el-dropdown-item>\r\n          </el-dropdown-menu>\r\n        </el-dropdown>\r\n      </div>\r\n\r\n      <div class=\"search_area right pointer\" style>\r\n        <template v-if=\"menuSearch\">\r\n          <el-autocomplete\r\n            style=\"width:20vw\"\r\n            icon=\"search\"\r\n            class=\"search_menu\"\r\n            v-model=\"state4\"\r\n            :fetch-suggestions=\"querySearchAsync\"\r\n            placeholder=\"请输入目录名\"\r\n            @select=\"handleSelect2\"\r\n          ></el-autocomplete>\r\n          <i class=\"el-icon-circle-close search_area_btn\" @click=\"closeSearchArea\"></i>\r\n        </template>\r\n        <template v-else>\r\n          <i class=\"el-icon-search search_area_btn\" @click=\"menuSearch=true\"></i>\r\n        </template>\r\n      </div>\r\n      <div class=\"header-right right pointer\" style>\r\n        <i class=\"el-icon-menu search_area_btn\" @click=\"toggleFull\"></i>\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<style scoped>\r\n/*Add \"scoped\" attribute to limit CSS to this component only */\r\n.main-header {\r\n  width: auto;\r\n}\r\n\r\n.main-title {\r\n  margin-right: 10px;\r\n  cursor: pointer;\r\n  float: left;\r\n  width: 25px;\r\n  height: 70px;\r\n}\r\n.pointer {\r\n  cursor: pointer;\r\n}\r\n.header-left .directImg {\r\n  border-radius: 0;\r\n  height: 18px;\r\n  width: 18px;\r\n  display: inline-block;\r\n  position: relative;\r\n  top: 7px;\r\n  vertical-align: middle;\r\n  transition: 1s;\r\n}\r\n.directImgNo {\r\n  transform: rotate(180deg);\r\n}\r\nh2 {\r\n  font-weight: normal;\r\n}\r\n.exit {\r\n  position: relative;\r\n  top: 8px;\r\n}\r\n.header {\r\n  display: flex;\r\n  height: 70px;\r\n  line-height: 70px;\r\n  background: #2a3245;\r\n  justify-content: space-between;\r\n}\r\n.header-progress {\r\n  flex: 1;\r\n  justify-content: center;\r\n}\r\n.header-progress .process-item {\r\n  display: initial;\r\n}\r\n.header-left {\r\n  margin-left: 20px;\r\n  width: 200px;\r\n  height: 70px;\r\n  overflow: hidden;\r\n  color: #cfd0d3;\r\n  font-size: 12px;\r\n}\r\n\r\n.header-left img {\r\n  width: 37px;\r\n  height: 37px;\r\n  border-radius: 18px;\r\n  float: left;\r\n  margin-top: 18px;\r\n}\r\n.header-left i.online,\r\n.whole-top i.online {\r\n  width: 12px;\r\n  height: 12px;\r\n  border-radius: 6px;\r\n  background: #5bea94;\r\n  position: absolute;\r\n  left: 57px;\r\n  top: 44px;\r\n  display: block;\r\n  float: left;\r\n  border: 2px solid #2a3245;\r\n}\r\n/* 加了不在线状态 */\r\n.header-left i.off-line,\r\n.whole-top i.off-line {\r\n  width: 9px;\r\n  height: 9px;\r\n  border-radius: 6px;\r\n  background: #5bea94;\r\n  position: absolute;\r\n  left: 57px;\r\n  top: 44px;\r\n  display: block;\r\n  float: left;\r\n  border: 2px solid #b9b9b9;\r\n}\r\n.header-left i.off-line,\r\n.chat-content .guest dt span.off-line,\r\n.chat-content .host dt span.off-line {\r\n  background: #b9b9b9;\r\n}\r\n.header-left span {\r\n  width: 8px;\r\n  height: 6px;\r\n  display: block;\r\n  float: left;\r\n  background: url(../assets/images/im_img/icon.png) -3px -3px no-repeat;\r\n  margin: 35px 12px 0 8px;\r\n  cursor: pointer;\r\n}\r\n.header-left b {\r\n  display: inline-block;\r\n  width: 146px;\r\n  text-overflow: ellipsis;\r\n  overflow: hidden;\r\n  white-space: nowrap;\r\n}\r\n.header-right {\r\n  margin-right: 15px;\r\n  color: #b5b8bf;\r\n}\r\n.header-right span {\r\n  margin-left: 10px;\r\n  font-size: 12px;\r\n}\r\n.header-right span i {\r\n  color: #575e6c;\r\n  margin-left: 13px;\r\n}\r\n/*加了一个手型 */\r\n.header-right span.manager {\r\n  border: 1px solid #575e6c;\r\n  border-radius: 12px;\r\n  padding: 3px 8px;\r\n  cursor: pointer;\r\n}\r\n.left {\r\n  float: left;\r\n}\r\n.header-right a,\r\n.left-tab ul li a {\r\n  color: #b5b8bf;\r\n}\r\na:hover.logout {\r\n  color: #00ffff;\r\n  text-decoration: underline;\r\n}\r\n.header-right .el-dropdown {\r\n  color: #b7c3d2;\r\n}\r\n.header-right .el-menu {\r\n  border-radius: 0;\r\n}\r\n</style>\r\n<script>\r\nimport auth from \"../utils/authenticate\";\r\nimport common from \"../utils/common\";\r\nimport { Notification } from \"element-ui\";\r\nimport \"../assets/style/base.css\";\r\nexport default {\r\n  props: [\"headObj\", \"menuTree\"],\r\n  data() {\r\n    return {\r\n      state4: \"\",\r\n      isFullscreen: false,\r\n      menuSearch: false,\r\n      title: common.title,\r\n      username: \"\",\r\n      searchBox: [],\r\n    };\r\n  },\r\n  created: function() {\r\n    this.username = auth.getUserName();\r\n  },\r\n  mounted() {},\r\n  methods: {\r\n    createStateFilter(queryString) {\r\n      return state => {\r\n        var key = state.value.indexOf(queryString.toLowerCase()) > -1;\r\n        return key;\r\n      };\r\n    },\r\n    addSearchBox(arr, key) {\r\n      for (var i = 0; i < arr.length; i++) {\r\n        if (arr[i].children) {\r\n          if (key) {\r\n            this.addSearchBox(arr[i].children, `${key}>${arr[i].title}`);\r\n          } else {\r\n            this.addSearchBox(arr[i].children, arr[i].title);\r\n          }\r\n        } else {\r\n          if (key) {\r\n            arr[i].value = `${key}>${arr[i].title}`;\r\n          } else {\r\n            arr[i].value = arr[i].title;\r\n          }\r\n          this.searchBox.push(arr[i]);\r\n        }\r\n      }\r\n    },\r\n    handleSelect2(item) {\r\n      this.$router.push({ path: \"/admin/\" + item.index });\r\n    },\r\n    querySearchAsync(queryString, cb) {\r\n      var searchBox = this.searchBox;\r\n      var results = queryString\r\n        ? searchBox.filter(this.createStateFilter(queryString))\r\n        : searchBox;\r\n      cb && cb(results);\r\n    },\r\n    closeSearchArea() {\r\n      this.state4 = \"\";\r\n      this.menuSearch = false;\r\n    },\r\n    toggleFull(e) {\r\n      if (!this.isFullscreen) {\r\n        (document.body.requestFullscreen &&\r\n          document.body.requestFullscreen()) ||\r\n          (document.body.mozRequestFullScreen &&\r\n            document.body.mozRequestFullScreen()) ||\r\n          (document.body.webkitRequestFullscreen &&\r\n            document.body.webkitRequestFullscreen()) ||\r\n          (document.body.msRequestFullscreen &&\r\n            document.body.msRequestFullscreen());\r\n      } else {\r\n        document.exitFullscreen\r\n          ? document.exitFullscreen()\r\n          : document.mozCancelFullScreen\r\n          ? document.mozCancelFullScreen()\r\n          : document.webkitExitFullscreen\r\n          ? document.webkitExitFullscreen()\r\n          : \"\";\r\n      }\r\n      this.isFullscreen = !this.isFullscreen;\r\n    },\r\n    toggleOpen() {\r\n      this.$emit(\"toggleOpen\");\r\n    },\r\n\r\n    alterPassword() {\r\n      this.$router.push({ path: \"/admin/resetPassword\" });\r\n    },\r\n    logout() {\r\n      var router = this.$router;\r\n\r\n      this.$confirm(\"此操作将退出当前系统, 是否继续?\", \"提示\", {\r\n        confirmButtonText: \"确定\",\r\n        cancelButtonText: \"取消\",\r\n        type: \"warning\",\r\n      })\r\n        .then(() => {\r\n          common.ajax({\r\n            url: \"/web/v1/user/logout\",\r\n            type: \"post\",\r\n            data: {},\r\n            contentType: \"application/json\",\r\n            success: function(response) {\r\n              auth.logout();\r\n              router.push({ path: \"/login\" });\r\n            },\r\n            error: function(response) {\r\n              var message = response.message ? response.message : \"未知错误\";\r\n              Notification.error({\r\n                title: \"错误\",\r\n                message: message,\r\n              });\r\n            },\r\n          });\r\n        })\r\n        .catch(() => {\r\n          this.$message({\r\n            type: \"success\",\r\n            message: \"已取消操作\",\r\n          });\r\n        });\r\n    },\r\n  },\r\n  watch: {\r\n    menuTree: {\r\n      handler(val, old_val) {\r\n        this.addSearchBox(JSON.parse(JSON.stringify(val)));\r\n      },\r\n      deep: true,\r\n    },\r\n  },\r\n};\r\n</script>\r\n\r\n"]}]);
