<template>
  <div class="header">
    <div class="header-left left main-header">
      <h2 @click="toggleOpen" class="main-title">
        <img
          src="../assets/images/menu_o.png"
          class="directImg"
          :class="{directImgNo:!headObj.isCollapse}"
          alt
        />
      </h2>
      <h2 style="float:left">{{title}}</h2>
    </div>

    <div class="header-right">
      <div class="right">
        <el-dropdown trigger="click">
          <span class="el-dropdown-link pointer">
            {{username}}
            <i class="el-icon-caret-bottom el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item @click.native="alterPassword()">修改密码</el-dropdown-item>
            <el-dropdown-item @click.native="logout">退出</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>

      <div class="search_area right pointer" style>
        <template v-if="menuSearch">
          <el-autocomplete
            style="width:20vw"
            icon="search"
            class="search_menu"
            v-model="state4"
            :fetch-suggestions="querySearchAsync"
            placeholder="请输入目录名"
            @select="handleSelect2"
          ></el-autocomplete>
          <i class="el-icon-circle-close search_area_btn" @click="closeSearchArea"></i>
        </template>
        <template v-else>
          <i class="el-icon-search search_area_btn" @click="menuSearch=true"></i>
        </template>
      </div>
      <div class="header-right right pointer" style>
        <i class="el-icon-menu search_area_btn" @click="toggleFull"></i>
      </div>
    </div>
  </div>
</template>

<style scoped>
/*Add "scoped" attribute to limit CSS to this component only */
.main-header {
  width: auto;
}

.main-title {
  margin-right: 10px;
  cursor: pointer;
  float: left;
  width: 25px;
  height: 70px;
}
.pointer {
  cursor: pointer;
}
.header-left .directImg {
  border-radius: 0;
  height: 18px;
  width: 18px;
  display: inline-block;
  position: relative;
  top: 7px;
  vertical-align: middle;
  transition: 1s;
}
.directImgNo {
  transform: rotate(180deg);
}
h2 {
  font-weight: normal;
}
.exit {
  position: relative;
  top: 8px;
}
.header {
  display: flex;
  height: 70px;
  line-height: 70px;
  background: #2a3245;
  justify-content: space-between;
}
.header-progress {
  flex: 1;
  justify-content: center;
}
.header-progress .process-item {
  display: initial;
}
.header-left {
  margin-left: 20px;
  width: 200px;
  height: 70px;
  overflow: hidden;
  color: #cfd0d3;
  font-size: 12px;
}

.header-left img {
  width: 37px;
  height: 37px;
  border-radius: 18px;
  float: left;
  margin-top: 18px;
}
.header-left i.online,
.whole-top i.online {
  width: 12px;
  height: 12px;
  border-radius: 6px;
  background: #5bea94;
  position: absolute;
  left: 57px;
  top: 44px;
  display: block;
  float: left;
  border: 2px solid #2a3245;
}
/* 加了不在线状态 */
.header-left i.off-line,
.whole-top i.off-line {
  width: 9px;
  height: 9px;
  border-radius: 6px;
  background: #5bea94;
  position: absolute;
  left: 57px;
  top: 44px;
  display: block;
  float: left;
  border: 2px solid #b9b9b9;
}
.header-left i.off-line,
.chat-content .guest dt span.off-line,
.chat-content .host dt span.off-line {
  background: #b9b9b9;
}
.header-left span {
  width: 8px;
  height: 6px;
  display: block;
  float: left;
  background: url(../assets/images/im_img/icon.png) -3px -3px no-repeat;
  margin: 35px 12px 0 8px;
  cursor: pointer;
}
.header-left b {
  display: inline-block;
  width: 146px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.header-right {
  margin-right: 15px;
  color: #b5b8bf;
}
.header-right span {
  margin-left: 10px;
  font-size: 12px;
}
.header-right span i {
  color: #575e6c;
  margin-left: 13px;
}
/*加了一个手型 */
.header-right span.manager {
  border: 1px solid #575e6c;
  border-radius: 12px;
  padding: 3px 8px;
  cursor: pointer;
}
.left {
  float: left;
}
.header-right a,
.left-tab ul li a {
  color: #b5b8bf;
}
a:hover.logout {
  color: #00ffff;
  text-decoration: underline;
}
.header-right .el-dropdown {
  color: #b7c3d2;
}
.header-right .el-menu {
  border-radius: 0;
}
</style>
<script>
import auth from "../utils/authenticate";
import common from "../utils/common";
import { Notification } from "element-ui";
import "../assets/style/base.css";
export default {
  props: ["headObj", "menuTree"],
  data() {
    return {
      state4: "",
      isFullscreen: false,
      menuSearch: false,
      title: common.title,
      username: "",
      searchBox: [],
    };
  },
  created: function() {
    this.username = auth.getUserName();
  },
  mounted() {},
  methods: {
    createStateFilter(queryString) {
      return state => {
        var key = state.value.indexOf(queryString.toLowerCase()) > -1;
        return key;
      };
    },
    addSearchBox(arr, key) {
      for (var i = 0; i < arr.length; i++) {
        if (arr[i].children) {
          if (key) {
            this.addSearchBox(arr[i].children, `${key}>${arr[i].title}`);
          } else {
            this.addSearchBox(arr[i].children, arr[i].title);
          }
        } else {
          if (key) {
            arr[i].value = `${key}>${arr[i].title}`;
          } else {
            arr[i].value = arr[i].title;
          }
          this.searchBox.push(arr[i]);
        }
      }
    },
    handleSelect2(item) {
      this.$router.push({ path: "/admin/" + item.index });
    },
    querySearchAsync(queryString, cb) {
      var searchBox = this.searchBox;
      var results = queryString
        ? searchBox.filter(this.createStateFilter(queryString))
        : searchBox;
      cb && cb(results);
    },
    closeSearchArea() {
      this.state4 = "";
      this.menuSearch = false;
    },
    toggleFull(e) {
      if (!this.isFullscreen) {
        (document.body.requestFullscreen &&
          document.body.requestFullscreen()) ||
          (document.body.mozRequestFullScreen &&
            document.body.mozRequestFullScreen()) ||
          (document.body.webkitRequestFullscreen &&
            document.body.webkitRequestFullscreen()) ||
          (document.body.msRequestFullscreen &&
            document.body.msRequestFullscreen());
      } else {
        document.exitFullscreen
          ? document.exitFullscreen()
          : document.mozCancelFullScreen
          ? document.mozCancelFullScreen()
          : document.webkitExitFullscreen
          ? document.webkitExitFullscreen()
          : "";
      }
      this.isFullscreen = !this.isFullscreen;
    },
    toggleOpen() {
      this.$emit("toggleOpen");
    },

    alterPassword() {
      this.$router.push({ path: "/admin/resetPassword" });
    },
    logout() {
      var router = this.$router;

      this.$confirm("此操作将退出当前系统, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          common.ajax({
            url: "/web/v1/user/logout",
            type: "post",
            data: {},
            contentType: "application/json",
            success: function(response) {
              auth.logout();
              router.push({ path: "/login" });
            },
            error: function(response) {
              var message = response.message ? response.message : "未知错误";
              Notification.error({
                title: "错误",
                message: message,
              });
            },
          });
        })
        .catch(() => {
          this.$message({
            type: "success",
            message: "已取消操作",
          });
        });
    },
  },
  watch: {
    menuTree: {
      handler(val, old_val) {
        this.addSearchBox(JSON.parse(JSON.stringify(val)));
      },
      deep: true,
    },
  },
};
</script>

