<template>
  <el-col>
    <el-menu
      :default-active="activeMenuIndex"
      default-active="1-4-1"
      background-color="#2a3245"
      text-color="#b7c3d2"
      active-text-color="#ffffff"
      @open="handleOpen"
      @select="handleSelect"
      :collapse="menuObj.isCollapse"
      :class="{openMenu:!menuObj.isCollapse,hideMenu:menuObj.isCollapse}"
    >
      <template v-for="menu in menuTree">
        <el-menu-item
          v-if="!menu.children"
          :key="menu.index"
          v-bind:index="menu.index"
        >{{menu.title}}</el-menu-item>
        <el-submenu v-else v-bind:index="menu.index" :key="menu.index">
          <template slot="title">
            <i :class="[menu.icon || 'fa fa-fw fa-navicon']"></i>
            <span class="firstTitle">{{menu.title}}</span>
            <i v-if="menu.isNew" :class="menu.isNew"></i>
          </template>
          <template v-for="submenu in menu.children">
            <el-menu-item
              v-if="!submenu.children"
              :key="submenu.index"
              v-bind:index="submenu.index"
            >
              <i :class="[submenu.icon || '']"></i>
              {{submenu.title}}
              <i v-if="menu.isNew" :class="menu.isNew"></i>
            </el-menu-item>
            <el-submenu v-else v-bind:index="submenu.index" :key="submenu.index">
              <template slot="title">
                <i :class="[submenu.icon || '']"></i>
                {{submenu.title}}
                <i v-if="menu.isNew" :class="menu.isNew"></i>
              </template>
              <el-menu-item
                v-for="threesubmenu in submenu.children"
                :key="threesubmenu.index"
                v-bind:index="threesubmenu.index"
              >{{threesubmenu.title}}</el-menu-item>
            </el-submenu>
          </template>
          <!--<el-menu-item v-for="submenu in menu.children" :key="submenu.index" v-bind:index="submenu.index">{{submenu.title}}</el-menu-item>-->
        </el-submenu>
      </template>
    </el-menu>
  </el-col>
</template>
<script>
import auth from "../utils/authenticate";
import menuTree from "../config/menu";
export default {
  props: ["menuObj"],
  data() {
    return {
      menuTree: [],
    };
  },
  created: function() {
    // 全量菜单 ==> 可见菜单
    if (!auth.isAdmin()) {
      this.menuTree = menuTree.filter(menu => {
        if (auth.hasPermission(menu.permission)) {
          if (menu.children) {
            menu.children = menu.children.filter(subMenu => {
              if (auth.hasPermission(subMenu.permission)) {
                return true;
              }
            });
          }
          return true;
        }
        return false;
      });
    } else {
      this.menuTree = menuTree;
      this.$emit("menuTreeChange", menuTree);
    }
  },
  computed: {
    activeMenuIndex() {
      var segments = this.$route.path.split("/");
      return segments[segments.length - 1];
    },
  },
  watch: {},
  methods: {
    handleOpen(key, keyPath) {},
    handleClose(key, keyPath) {},
    handleSelect(key, keyPath) {
      this.$router.push({ path: "/admin/" + key });
    },
  },
};
</script>
<style>
.openMenu {
  width: 220px;
}
.hidemenu {
  width: auto;
}
</style>

<style scoped>
/*---滚动条默认显示样式--*/
::-webkit-scrollbar-thumb {
  background-color: #373f52;
  height: 50px;
  outline-offset: -2px;
  outline: 2px solid #1f2d3d;
  -webkit-border-radius: 4px;
  border: 2px solid #1f2d3d;
}

/*---鼠标点击滚动条显示样式--*/
::-webkit-scrollbar-thumb:hover {
  background-color: #b2b2b3;
  height: 50px;
  -webkit-border-radius: 4px;
}

/*---滚动条大小--*/
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

/*---滚动框背景样式--*/
::-webkit-scrollbar-track-piece {
  background-color: #1f2f3d;
  -webkit-border-radius: 8px;
}

::-webkit-scrollbar-thumb:active {
  height: 50px;
  background-color: #b2b2b3;
  -webkit-border-radius: 4px;
}
.new-flag {
  content: "new";
  color: red;
}
.new-flag:after {
  content: "new";
  color: red;
  font-size: 12px;
  margin-top: -5px;
}
</style>
